import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm } from "redux-form";
import { getJenisLabel } from "../actions/pengaturan";
import { AxiosMasterGet } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { formatGram, formatRp } from "../helper";

class AccordListBarangSet extends Component {
  constructor(props) {
    super(props);
    this.state = {
      detail: [],
      columns: [
        {
          dataField: "kode_barcode_set",
          text: "Kode Barcode",
        },
        {
          dataField: "total_bruto",
          text: "Total Bruto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "total_netto",
          text: "Total Netto",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "status_active",
          text: "Status",
          align: "center",
          formatter: (rowContent, row) => {
            if (row.status_active === true) {
              return (
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-lg-3"></div>
                    <div className="badge-pill badge-success col-lg-6 text-center">
                      AVAILABLE
                    </div>
                  </div>
                </div>
              );
            } else
              return (
                <div className="col-lg-12">
                  <div className="row">
                    <div className="col-lg-3"></div>
                    <div className="badge-pill badge-danger col-lg-6 text-center">
                      NOT-AVAILABLE
                    </div>
                  </div>
                </div>
              );
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="col-lg-12">
                <div className="row">
                  <div className="col-lg-6 text-center">
                    <button
                      className="btn btn-warning mr-2"
                      data-tut="reactour_3"
                      type="button"
                      onClick={() => this.print(row)}
                    >
                      Print
                    </button>
                  </div>
                  <div className="col-lg-6 text-center">
                    <button
                      className="btn btn-outline-primary"
                      data-tut="reactour_3"
                      data-toggle="modal"
                      data-target="#detail_barangSet"
                      onClick={() =>
                        this.setState({
                          detail: row.detail_barang,
                        })
                      }
                    >
                      Detail
                    </button>
                  </div>
                </div>
              </div>
            );
          },
        },
      ],
      columns2: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kadar_cetak",
          text: "Cap\nKode",
          align: "right",
          headerAlign: "right",
        },
        // {
        //   dataField: "plastik",
        //   text: "P",
        // },
        // {
        //   dataField: "kulit",
        //   text: "K",
        // },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "kadar_jual",
          text: "Kode\nTukar",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc_persen",
          text: "%",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "disc",
          text: "Disc\n(Gr)",
          align: "right",
          headerAlign: "right",

          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "netto_disc",
          text: "Netto\nDisc(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(Rp)",
          align: "right",
          headerAlign: "right",
        },
      ],
    };
  }

  print(data) {
    if (this.props.jenis_label === "Label 2") {
      this.handleBarcode(data.kode_barcode_set);
      return false;
    }
    let feedback = "";
    feedback += `<xpml><page quantity='0' pitch='35.0 mm'></xpml>I8,A\n`;
    feedback += `ZN\n`;
    feedback += `q504\n`;
    feedback += `O\n`;
    feedback += `JF\n`;
    feedback += `KIZZQ0\n`;
    feedback += `KI9+0.0\n`;
    feedback += `ZT\n`;
    feedback += `Q280,B25\n`;
    feedback += `Arglabel 350 31\n`;
    feedback += `exit\n`;
    feedback += `KI80\n`;
    feedback += `<xpml></page></xpml><xpml><page quantity='1' pitch='35.0 mm'></xpml>N\n`;
    feedback += `A400,275,2,2,1,2,N,"BARCODE SET"\n`;
    // BARCODE
    feedback += `B350,238,2,1,1,10,73,N,"${data.kode_barcode_set}"\n`;
    // BARCODE
    feedback += `A400,155,2,2,1,2,N,"${data.kode_barcode_set}"\n`;
    feedback += `A400,107,2,2,1,2,N,"Bruto"\n`;
    feedback += `A400,62,2,2,1,2,N,"Netto"\n`;
    feedback += `A325,107,2,2,1,2,N,"${parseFloat(data.total_bruto).toFixed(
      3
    )}"\n`;
    feedback += `A325,62,2,2,1,2,N,"${parseFloat(data.total_netto).toFixed(
      3
    )}"\n`;
    feedback += `P1\n`;
    feedback += `<xpml></page></xpml><xpml><end/></xpml>`;
    document.getElementById("myInput").value = feedback;
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  }

  handleBarcode = async (kode_barcode_set) => {
    await AxiosMasterGet(
      "barang-set/get-by-kode-barcode-set-active/" + kode_barcode_set
    ).then((res) => {
      let data = res.data.detail_barang;
      data.forEach((element) => {
        var simpan_nota_1 =
          JSON.parse(localStorage.getItem("barcode_barangSet_level1")) || [];
        var posisi = simpan_nota_1.findIndex(
          (list) => list.kode_kategori === element.kode_kategori
        );
        if (posisi !== -1) {
          var nota_1 = {
            netto_disc: formatGram(
              parseFloat(element.netto_disc || 0) +
                parseFloat(simpan_nota_1[posisi].netto_disc),
              3
            ),
            netto: formatGram(
              parseFloat(element.netto || 0) +
                parseFloat(simpan_nota_1[posisi].netto),
              3
            ),
            ongkos_rp: formatRp(
              parseFloat(
                element.ongkos_rp === undefined ? 0 : element.ongkos_rp
              ) + simpan_nota_1[posisi].ongkos_rp
            ), //ongkos
            disc: formatGram(
              parseFloat(element.disc || 0) +
                parseFloat(simpan_nota_1[posisi].disc)
            ),
            bruto: formatGram(
              parseFloat(element.bruto || 0) +
                parseFloat(simpan_nota_1[posisi].bruto)
            ), //berat
            kode_kategori: element.kode_kategori,
            kode_tukar: element.kode_tukar,
            kode_kelompok: element.kode_kelompok,
            keterangan: element.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              element.harga_jual === "undefined" ? 0 : element.harga_jual,
          };
          simpan_nota_1.push(nota_1);
          simpan_nota_1.splice(posisi, 1);
          localStorage.setItem(
            "barcode_barangSet_level1",
            JSON.stringify(simpan_nota_1)
          );
        } else {
          var nota_1_else = {
            netto_disc: formatGram(parseFloat(element.netto_disc || 0), 3),
            netto: formatGram(parseFloat(element.netto || 0), 3), //murni
            ongkos_rp: parseFloat(
              element.ongkos_rp === undefined ? 0 : element.ongkos_rp
            ), //ongkos
            disc: element.disc || 0,
            bruto: formatGram(parseFloat(element.bruto || 0)), //berat
            kode_kategori: element.kode_kategori,
            kode_kelompok: element.kode_kelompok,
            kode_tukar: element.kode_tukar,
            keterangan: element.keterangan,
            disc_persen: element.disc_persen || 0,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              element.harga_jual === "undefined"
                ? 0
                : parseFloat(element.harga_jual) * parseFloat(element.qty),
          };
          simpan_nota_1.push(nota_1_else);
          localStorage.setItem(
            "barcode_barangSet_level1",
            JSON.stringify(simpan_nota_1)
          );
        }
      });
    });
    this.printBarcode(kode_barcode_set);
  };

  printBarcode = (kode_barcode_set) => {
    let data = JSON.parse(localStorage.getItem("barcode_barangSet_level1"));
    let total = data
      .map((x) => parseFloat(x.netto_disc))
      .reduce((a, b) => a + b, 0);
    let kode_kelompok = data[0]?.kode_kelompok;
    let row1 = data[0]?.kode_kategori;
    let berat1 = data[0]?.netto_disc;
    let row2 = data[1]?.kode_kategori || "";
    let berat2 = data[2]?.netto_disc || "";
    let row3 = "";
    let berat3 = "";
    for (let i = 2; i < data.length; i++) {
      row3 += (row3 === "" ? "" : ", ") + data[i].kode_kategori;
      berat3 += parseFloat(data[i].netto_disc);
    }
    let feedback = "";
    feedback += `I8,A\n`;
    feedback += `ZN\n`;
    feedback += `q652\n`;
    feedback += `O\n`;
    feedback += `JF\n`;
    feedback += `KIZZQ0\n`;
    feedback += `KI9+0.0\n`;
    feedback += `ZT\n`;
    feedback += `Q160,B25\n`;
    feedback += `Arglabel 200 31\n`;
    feedback += `exit\n`;
    feedback += `KI80\n`;
    feedback += `N\n`;
    feedback += `B226,106,2,1,1,2,27,N,"${kode_barcode_set}"\n`;
    feedback += `A226,74,2,1,1,1,N,"${kode_barcode_set}"\n`;
    feedback += `B226,56,2,1B,1,2,30,N,"AYU-${formatGram(
      total,
      3
    )}-${formatGram(total, 3)}"\n`;
    feedback += `A226,21,2,1,1,1,N,"AYU-${formatGram(total, 3)}-${formatGram(
      total,
      3
    )}"\n`;
    feedback += `LO408,83,218,3\n`;
    feedback += `LO409,23,215,3\n`;
    feedback += `A624,101,2,1,1,1,N,"${kode_kelompok}"\n`;
    feedback += `A439,101,2,1,1,1,N,"AYU"\n`;
    feedback += `A624,78,2,1,1,1,N,"${row1}"\n`;
    feedback += `A469,78,2,1,1,1,N,"${formatGram(berat1, 3)}"\n`;
    if ((data.length = 2)) {
      feedback += `A624,61,2,1,1,1,N,"${row2}"\n`;
      feedback += `A469,61,2,1,1,1,N,"${
        berat2 === "" ? "" : formatGram(berat2, 3)
      }"\n`;
    }
    if ((data.length = 3)) {
      feedback += `A624,42,2,1,1,1,N,"${row3}"\n`;
      feedback += `A460,42,2,1,1,1,N,"${
        berat3 === "" ? "" : formatGram(berat3, 3)
      }"\n`;
    }
    feedback += `A626,18,2,1,1,1,N,"Total"\n`;
    feedback += `A470,18,2,1,1,1,N,"${formatGram(total, 3)}"\n`;
    feedback += `P1\n`;
    document.getElementById("myInput").value = feedback;
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  };

  componentDidMount() {
    this.props.dispatch(getJenisLabel());
  }

  render() {
    return (
      <div className="col-lg-12 mt-2">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-primary">
            <div className="card-header" id="headingOne">
              <div className="row">
                <div className="col-lg-12">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseBarangSet"
                      aria-expanded="true"
                      aria-controls="collapseBarangSet"
                    >
                      List Barang Set
                    </a>
                  </h5>
                </div>
              </div>
            </div>
            <div
              id="collapseBarangSet"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                    <div className="d-none">
                      <textarea id="myInput"></textarea>
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="kodeJenis"
                        data={this.props.BarangSet || []}
                        columns={this.state.columns}
                        textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                      />
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
        <div
          id="detail_barangSet"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-xl">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Detail Barang Set
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <div className="col-lg-12">
                  <GlobalTabel
                    keyField="kodeJenis"
                    data={this.state.detail || []}
                    columns={this.state.columns2}
                    textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                  />
                </div>
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AccordListBarangSet = reduxForm({
  form: "AccordListBarangSet",
  enableReinitialize: true,
})(AccordListBarangSet);
export default connect((state) => {
  return {
    BarangSet: state.provinsi.BarangSet,
    jenis_label: state.provinsi.jenis_label,
  };
})(AccordListBarangSet);
