import React, { Component, lazy, Suspense } from "react";
import { connect } from "react-redux";
import { Field, reduxForm, reset } from "redux-form";
import { getUUID } from "../actions/fakturAction";
import Swal from "sweetalert2";
import { getListTransaksiPenjualan } from "../actions/transaksiPenjualanAction";
import { finishSend, getNetto, progressSend } from "../actions/LogicAction";
import PrintNota from "../Stocking/Component/PrintNota";
import { gagal, warning } from "../Alert";
import ReactTooltip from "react-tooltip";
import { getTotalNW } from "../actions/returnPenjualanAction";
import Skeleton from "react-loading-skeleton";
import { showModal } from "../actions/ModalAction";
import HeaderCard from "../HeaderCard";
import { AxiosMasterGet, AxiosMasterPost } from "../Axios";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { renderField, renderFieldSelect } from "../renderField";
import { getBarangSet, getListBarangSet } from "../actions/barangAction";
import { getToday } from "../GlobalComponent/function";
import ListBarangSet from "./ListBarangSet";
import { Link } from "react-router-dom";
import { formatGram, formatRp } from "../helper";
import { getcustomerList } from "../actions/customerAction";

const ModalMasterData = lazy(() =>
  import("../DataMaster/Component/ModalMasterData")
);
const FormBarangSet = lazy(() => import("./Component/FormBarangSet"));
const maptostate = (state) => {
  return {
    noFaktur: state.provinsi.noFaktur,
    listCustomer: state.provinsi.customer,
    barangSet_temp: state.provinsi.barangSet_temp,
    listLokasi: state.provinsi.lokasi,
    listSales: state.provinsi.salesman,
    totalNW: state.provinsi.totalNW,
    username: state.provinsi.username,
    onSend: state.provinsi.onSend,
    kodeBarang: state.provinsi.kodeBarang,
    listBarangSet: state.provinsi.listBarangSet,
  };
};
class BarangSet extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listLokasi: [],
      listSales: [],
      listCustomer: [],
      kodeCustomer: "",
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kadar_cetak",
          text: "Cap\nKode",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "p",
          text: "P",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "k",
          text: "K",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "harga_jual",
          text: "Harga Jual",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, rowIndex) => {
            return (
              <div className="text-center">
                <button
                  data-tip
                  data-for="hapus"
                  className="btn btn-danger"
                  onClick={() => this.deleteRow(rowIndex, row.netto_disc)}
                >
                  <ReactTooltip id="hapus" type="dark" effect="solid">
                    <span>Hapus</span>
                  </ReactTooltip>
                  <i className="fa fa-trash"></i>
                </button>
                <br />
              </div>
            );
          },
        },
      ],
      discGlobalGr: 0,
      discGlobalPr: 0,
      totalNWExt: 0,
      noBon: "",
      namaCustomer: "",
      lokasi: "",
      sales: "",
      namaToko: "",
      tanggal: "",
      data: "",
    };
  }

  deleteRow(rowIndex, dihapus) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        const array = JSON.parse(localStorage.getItem("barangSet_temp")) || [];
        array.splice(rowIndex, 1);
        localStorage.setItem("barangSet_temp", JSON.stringify(array));
        var awal = localStorage.getItem("GrandTotalNetto");
        var hasil = parseFloat(awal) - parseFloat(dihapus);
        localStorage.setItem(
          "GrandTotalNetto",
          formatGram(parseFloat(hasil), 3)
        );
        Swal.fire({
          text: "Berhasil",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
          icon: "success",
        })
          .then(() => this.props.dispatch(getTotalNW()))
          .then(() => this.props.dispatch(getListBarangSet()))
          .then(() =>
            this.setState({
              GrandTotalNetto: formatGram(
                parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0,
                3
              ),
            })
          );
      }
    });
  }
  componentDidMount() {
    this.props.change("tanggal", getToday());
    this.props.change("total_netto", localStorage.getItem("TotalBruto"));
    this.props.change("total_bruto", localStorage.getItem("TotalNetto"));
    this.props.dispatch(getUUID());
    this.props.dispatch(getListBarangSet());
    this.props.dispatch(getBarangSet());
    this.props.dispatch(getcustomerList());
    localStorage.removeItem("TotalBruto");
    localStorage.removeItem("TotalNetto");
    localStorage.removeItem("barangSet_temp");
    localStorage.removeItem("barcode_barangSet_level1");
    localStorage.removeItem("customer_1");
    this.setState({
      GrandTotalNetto: formatGram(
        parseFloat(localStorage.getItem("GrandTotalNetto") || 0) - 0,
        3
      ),
    });
  }

  handleSubmit(data) {
    let param = data.kodeBarang;
    let hasil = this.props.kodeBarang.filter(
      (data) => data.kode_jenis === param
    );
    AxiosMasterGet(`jenis/get/by-kode-jenis/${data.kodeBarang}`)
      .then((res) => {
        var simpan_nota_1 =
          JSON.parse(localStorage.getItem("barcode_barangSet_level1")) || [];
        console.log(data.kodeKategori);
        var posisi = simpan_nota_1.findIndex(
          (list) => list.kode_kategori === data.kodeKategori
        );
        if (posisi !== -1) {
          var nota_1 = {
            netto_disc: formatGram(
              parseFloat(data.nettoDiscGlobalExt || 0) +
                parseFloat(simpan_nota_1[posisi].netto_disc),
              3
            ),
            netto: formatGram(
              parseFloat(data.netto || 0) +
                parseFloat(simpan_nota_1[posisi].netto),
              3
            ),
            ongkos_rp: formatGram(
              parseFloat(data.rp === undefined ? 0 : data.rp) +
                simpan_nota_1[posisi].ongkos_rp
            ).toLocaleString("id-ID"), //ongkos
            disc: formatGram(
              parseFloat(data.discGlobalGr || 0) +
                parseFloat(simpan_nota_1[posisi].disc)
            ),
            bruto: formatGram(
              parseFloat(data.bruto || 0) +
                parseFloat(simpan_nota_1[posisi].bruto)
            ), //berat
            kode_kategori: data.kodeKategori,
            kode_tukar: data.kadarTransaksi,
            kode_kelompok: res.data.kode_kelompok,
            keterangan: data.keterangan,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
          };
          simpan_nota_1.push(nota_1);
          simpan_nota_1.splice(posisi, 1);
          localStorage.setItem(
            "barcode_barangSet_level1",
            JSON.stringify(simpan_nota_1)
          );
        } else {
          var nota_1_else = {
            netto_disc: formatGram(parseFloat(data.nettoDiscGlobalExt || 0), 3),
            netto: formatGram(parseFloat(data.netto || 0), 3), //murni
            ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
            disc: data.discGlobalGr || 0,
            bruto: formatGram(parseFloat(data.bruto || 0)), //berat
            kode_kategori: data.kodeKategori,
            kode_kelompok: res.data.kode_kelompok,
            kode_tukar: data.kadarTransaksi,
            keterangan: data.keterangan,
            disc_persen: data.discGlobalPr || 0,
            jumlah_barang_set: 0,
            total_berat_barang_set: 0,
            kode_barcode_set: "-",
            harga_jual:
              data.harga_jual === "undefined"
                ? 0
                : parseFloat(data.harga_jual) * parseFloat(data.qty),
          };
          simpan_nota_1.push(nota_1_else);
          localStorage.setItem(
            "barcode_barangSet_level1",
            JSON.stringify(simpan_nota_1)
          );
        }
        var simpan = {
          netto_disc:
            formatGram(parseFloat(data.nettoDiscGlobalExt || 0), 3) || 0,
          gross: formatGram(parseFloat(data.gross || 0)) || 0, //berat + bungkus
          netto: formatGram(parseFloat(data.netto || 0), 3) || 0, //murni
          qty: data.qty || 1,
          k: parseFloat(data.k || 0),
          ongkos_rp: parseFloat(data.rp === undefined ? 0 : data.rp), //ongkos
          kadar_cetak: (hasil[0].kadar_cetak || 0).toString(),
          disc_persen: data.discGlobalPr || 0,
          kadar_jual: parseFloat(data.kadarTransaksi || 0), //harga%
          kadar_level2: res.data.level2 || 0,
          kadar_level3: res.data.level3 || 0,
          kadar_modal: hasil[0].kadar_beli || 0, //harga%
          kode_jenis: hasil[0].kode_jenis || 0, //kodebarang
          kode_barcode: "-",
          p: parseFloat(data.p || 0),
          disc: data.discGlobalGr || 0,
          pkg: parseFloat(data.pkg || 0),
          bruto: formatGram(parseFloat(data.bruto || 0)), //berat
          harga_jual: data.harga_jual === "undefined" ? 0 : data.harga_jual,
        };
        var array = JSON.parse(localStorage.getItem("barangSet_temp")) || [];
        array.push(simpan);

        var TotalNetto = array
          .map((list) => parseFloat(list.netto_disc))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("TotalNetto", TotalNetto);
        var TotalBruto = array
          .map((list) => parseFloat(list.bruto))
          .reduce((a, b) => a + b, 0);
        localStorage.setItem("TotalBruto", TotalBruto);
        localStorage.setItem("barangSet_temp", JSON.stringify(array));

        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        });
        this.props.dispatch(getListBarangSet());
        this.props.dispatch(getTotalNW());
        this.props.dispatch(reset("FormBarangSet"));

        this.props.dispatch(getNetto("barangSet_temp"));
        this.props.change("total_netto", TotalBruto);
        this.props.change("total_bruto", TotalNetto);
      })
      .catch((err) =>
        gagal("Kode Kelompok Belum Disesuaikan, Mohon Sesuaikan Dahulu")
      );
  }

  transaksiPenjualanSubmit() {
    if (localStorage.getItem("barangSet_temp") === null) {
      warning("Barang masih kosong, Silahkan tambah barang");
      return false;
    }
    let kode_barcode_set = Math.random().toString().substr(2, 14);
    this.handleBarcode(kode_barcode_set);
    var simpan = {
      no_barang_set: this.props.noFaktur,
      kode_barcode_set: kode_barcode_set,
      total_bruto: localStorage.getItem("TotalBruto"),
      total_netto: localStorage.getItem("TotalNetto"),
      detail_barang: JSON.parse(localStorage.getItem("barangSet_temp")),
    };
    this.props.dispatch(progressSend());
    // INISIALISASI AUTOTABLE
    const tableRows = [];
    var table = JSON.parse(localStorage.getItem("barangSet_temp"));
    // for each ticket pass all its data into an array
    table.forEach((ticket, i) => {
      const ticketData = [
        ++i,
        ticket.kadar_cetak,
        ticket.kode_jenis,
        formatGram(parseFloat(ticket.bruto)),
        ticket.kadar_jual,
        formatGram(parseFloat(ticket.disc)),
        formatGram(parseFloat(ticket.netto), 3),
        ticket.pkg,
        ticket.qty,
        formatRp(parseFloat(ticket.ongkos_rp)),
        "",
      ];
      // push each tickcet's info into a row
      tableRows.push(ticketData);
    });
    const footer = [
      "",
      "",
      "",
      formatGram(
        table.map((list) => parseFloat(list.bruto)).reduce((a, b) => a + b, 0)
      ),
      "",
      formatGram(
        table.map((list) => parseFloat(list.disc)).reduce((a, b) => a + b, 0)
      ),
      formatGram(
        table.map((list) => parseFloat(list.netto)).reduce((a, b) => a + b, 0),
        3
      ),
      table.map((list) => parseFloat(list.pkg)).reduce((a, b) => a + b, 0),
      table.map((list) => parseFloat(list.qty)).reduce((a, b) => a + b, 0),
      formatRp(
        table
          .map((list) => parseFloat(list.ongkos_rp))
          .reduce((a, b) => a + b, 0)
      ),
      this.state.totalNWExt,
    ];
    tableRows.push(footer);
    console.log(JSON.stringify(tableRows));
    var columTable = [
      [
        "No",
        "Kadar",
        "Kode",
        "Bruto \n(Gr)",
        "Kode\nTukar",
        "Diskon",
        "Netto \n(Gr)",
        "Pkg",
        "Qty",
        "Ongkos \n(Rp)",
        "Grand Total\nNetto Ext",
      ],
    ];
    // AKHIR INISIALISASI AUTOTABLE
    AxiosMasterPost("barang-set/add", simpan)
      .then(() =>
        Swal.fire({
          position: "top-end",
          icon: "success",
          text: "Data Berhasil Ditambahkan!",
          showConfirmButton: false,
          timer: 1500,
        })
          .then(() =>
            PrintNota(
              "No Trx",
              this.props.noFaktur.toUpperCase(),
              "",
              "",
              "Kode Barcode",
              kode_barcode_set,
              "",
              "",
              "ADMIN",
              this.state.tanggal,
              "",
              columTable,
              "Barang Set",
              tableRows
            )
          )
          .then(() => this.props.dispatch(finishSend()))
          .then(() => localStorage.removeItem("barangSet_temp"))
          .then(() => localStorage.removeItem("TotalNetto"))
          .then(() => localStorage.removeItem("TotalBruto"))
          .then(() => localStorage.removeItem("GrandTotalNetto"))
          .then(() => localStorage.removeItem("noFaktur"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListTransaksiPenjualan()))
          .then(() =>
            this.setState({
              noBon: "",
              namaCustomer: "",
              lokasi: "",
              sales: "",
              namaToko: "",
              tanggal: "",
              toko: "",
              GrandTotalNetto: 0,
            })
          )
      )
      .catch((err) =>
        Swal.fire({
          position: "top-end",
          icon: "error",
          text: `${err.response && err.response.data}`,
          showConfirmButton: false,
          timer: 1500,
        }).then(() => this.props.dispatch(finishSend()))
      );
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  handleBarcode = (kode_barcode_set) => {
    let data = JSON.parse(localStorage.getItem("barcode_barangSet_level1"));
    let total = data
      .map((x) => parseFloat(x.netto_disc))
      .reduce((a, b) => a + b, 0);
    let kode_kelompok = data[0]?.kode_kelompok;
    let row1 = data[0]?.kode_kategori;
    let berat1 = data[0]?.netto_disc;
    let row2 = data[1]?.kode_kategori || "";
    let berat2 = data[2]?.netto_disc || "";
    let row3 = "";
    let berat3 = "";
    for (let i = 2; i < data.length; i++) {
      row3 += (row3 === "" ? "" : ", ") + data[i].kode_kategori;
      berat3 += parseFloat(data[i].netto_disc);
    }
    let feedback = "";
    feedback += `I8,A\n`;
    feedback += `ZN\n`;
    feedback += `q652\n`;
    feedback += `O\n`;
    feedback += `JF\n`;
    feedback += `KIZZQ0\n`;
    feedback += `KI9+0.0\n`;
    feedback += `ZT\n`;
    feedback += `Q160,B25\n`;
    feedback += `Arglabel 200 31\n`;
    feedback += `exit\n`;
    feedback += `KI80\n`;
    feedback += `N\n`;
    feedback += `B226,106,2,1,1,2,27,N,"${kode_barcode_set}"\n`;
    feedback += `A226,74,2,1,1,1,N,"${kode_barcode_set}"\n`;
    feedback += `B226,56,2,1B,1,2,30,N,"AYU-${formatGram(total)}-${formatGram(
      total,
      3
    )}"\n`;
    feedback += `A226,21,2,1,1,1,N,"AYU-${formatGram(total)}-${formatGram(
      total,
      3
    )}"\n`;
    feedback += `LO408,83,218,3\n`;
    feedback += `LO409,23,215,3\n`;
    feedback += `A624,101,2,1,1,1,N,"${kode_kelompok}"\n`;
    feedback += `A439,101,2,1,1,1,N,"AYU"\n`;
    feedback += `A624,78,2,1,1,1,N,"${row1}"\n`;
    feedback += `A469,78,2,1,1,1,N,"${formatGram(berat1, 3)}"\n`;
    if ((data.length = 2)) {
      feedback += `A624,61,2,1,1,1,N,"${row2}"\n`;
      feedback += `A469,61,2,1,1,1,N,"${
        berat2 === "" ? "" : formatGram(berat2, 3)
      }"\n`;
    }
    if ((data.length = 3)) {
      feedback += `A624,42,2,1,1,1,N,"${row3}"\n`;
      feedback += `A460,42,2,1,1,1,N,"${
        berat3 === "" ? "" : formatGram(berat3, 3)
      }"\n`;
    }
    feedback += `A626,18,2,1,1,1,N,"Total"\n`;
    feedback += `A470,18,2,1,1,1,N,"${formatGram(total, 3)}"\n`;
    feedback += `P1\n`;
    document.getElementById("myInput").value = feedback;
    const element = document.createElement("a");
    const file = new Blob([document.getElementById("myInput").value], {
      type: "text/plain;charset=utf-8",
    });
    element.href = URL.createObjectURL(file);
    element.download = "autoprint_barcode.txt";
    document.body.appendChild(element);
    element.click();
  };

  inputGrandTotal = (input) => (e) => {
    var GrandNetto = this.props.sumGross - parseFloat(this.state.discGlobalGr);
    var grandTotal = e.target.value;
    console.log(GrandNetto + "-" + grandTotal);
    if (GrandNetto - grandTotal < -1) {
      warning("Selisih lebih dari 1");
      e.target.value = "";
      return false;
    } else {
      if (GrandNetto - grandTotal <= 1) {
        this.setState({
          [input]: e.target.value,
        });
      } else {
        warning("Selisih lebih dari 1");
        e.target.value = "";
        return false;
      }
    }
  };
  discount = (input) => (e) => {
    var hasil =
      (parseFloat(e.target.value) / 100) *
      parseFloat(localStorage.getItem("GrandTotalNetto"));
    this.setState({
      discGlobalGr: formatGram(hasil),
      discGlobalPr: e.target.value,
      GrandTotalNetto: formatGram(
        parseFloat(localStorage.getItem("GrandTotalNetto")) - formatGram(hasil),
        3
      ),
    });
  };
  onSubmit(data, input) {
    this.setState({
      [input]: data,
    });
  }
  onSubmitToko(data, input) {
    var split = data.split("|");
    this.setState({
      kodeCustomer: split[0],
      namaCustomer: split[1],
      namaToko: split[2],
      toko: data,
    });
  }

  render() {
    return (
      <div className="container-fluid">
        <ol className="breadcrumb mb-2">
          <Link to="/KlasifikasiBarang">
            <button
              type="button"
              className="btn btn-secondary "
              data-tut="reactour_5"
            >
              <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Back
            </button>
          </Link>
        </ol>
        <div className="col-lg-12">
          <div className="d-none">
            <textarea id="myInput"></textarea>
          </div>
          <div className="card">
            <HeaderCard title="Barang Set" />
            <div className="card-body">
              <div className="row">
                <div className="col-lg-12 col-md-12 mt-3">
                  <div className="row">
                    <div className="col-lg-12">
                      <ListBarangSet />
                    </div>
                    <div className="col-lg-4">
                      <Field
                        name="tanggal"
                        component={renderField}
                        type="date"
                        label="Tanggal"
                        placeholder="Masukan Tanggal"
                      />
                    </div>
                    <div className="col-lg-4 d-none">
                      <Field
                        name="level_customer_1"
                        component={renderFieldSelect}
                        options={this.props.listCustomer
                          .filter((fil) => fil.level_customer === "LEVEL1")
                          .map((obj) => {
                            var payload = {
                              value: obj.kode_customer,
                              name: obj.nama_toko + " - " + obj.nama_customer,
                            };
                            return payload;
                          })}
                        type="text"
                        label="Level Customer 1"
                        placeholder="Level Customer 1"
                        onChange={(data) =>
                          localStorage.setItem("customer_1", data)
                        }
                      />
                    </div>
                    <div className="col-lg-4"></div>
                    <div className="col-lg-6">
                      <Field
                        name="total_netto"
                        component={renderField}
                        type="text"
                        label="Total Netto"
                        placeholder="Masukan Total Netto"
                        readOnly
                      />
                    </div>
                    <div className="col-lg-6">
                      <Field
                        name="total_bruto"
                        component={renderField}
                        type="text"
                        label="Total Bruto"
                        placeholder="Masukan Total Bruto"
                        readOnly
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.props.dispatch(showModal())}
                        >
                          Tambah Barang
                          <i class="fa fa-plus ml-3" aria-hidden="true"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-lg-12 mt-5">
                  <GlobalTabel
                    keyField="id"
                    data={this.props.listBarangSet}
                    columns={this.state.columns}
                  />
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      disabled={this.props.onSend}
                      onClick={() => this.transaksiPenjualanSubmit()}
                    >
                      {this.props.onSend ? (
                        <span
                          className="spinner-border spinner-border-sm mr-3"
                          aria-hidden="true"
                        ></span>
                      ) : null}
                      Selesai
                      <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <ModalMasterData
          content={
            <Suspense
              fallback={<Skeleton width={"100%"} height={50} count={4} />}
            >
              <FormBarangSet onSubmit={(data) => this.handleSubmit(data)} />
            </Suspense>
          }
          title="Tambah Data"
        />
      </div>
    );
  }
}

BarangSet = reduxForm({
  form: "BarangSet",
  enableReinitialize: true,
})(BarangSet);
export default connect(maptostate, null)(BarangSet);
